/**
 * 错误类定义
 * 
 * 功能说明：
 * - 定义各种业务错误类型
 * - 提供统一的错误处理机制
 * - 支持错误码和详细信息
 * 
 * @author WMS Cloud Team
 * @version 1.0.0
 */

/**
 * 错误基类
 */
export abstract class MCPError extends Error {
  abstract readonly code: string;
  abstract readonly type: string;

  constructor(
    message: string,
    public readonly details?: any
  ) {
    super(message);
    this.name = this.constructor.name;
    
    // 确保错误堆栈正确显示
    if (Error.captureStackTrace) {
      Error.captureStackTrace(this, this.constructor);
    }
  }

  /**
   * 转换为MCP错误响应格式
   */
  toMCPError(): {
    code: string;
    message: string;
    type: string;
    details?: any;
  } {
    return {
      code: this.code,
      message: this.message,
      type: this.type,
      details: this.details
    };
  }

  /**
   * 转换为JSON格式
   */
  toJSON(): any {
    return {
      name: this.name,
      code: this.code,
      type: this.type,
      message: this.message,
      details: this.details,
      stack: this.stack
    };
  }
}

/**
 * 数据库连接错误
 */
export class DatabaseError extends MCPError {
  readonly type = 'DATABASE_ERROR';

  constructor(
    public readonly code: string,
    message: string,
    details?: any
  ) {
    super(message, details);
  }
}

