/**
 * 根据错误代码获取默认错误消息
 */
export function getErrorMessage(code: string): string {
  return ErrorMessages[code as keyof typeof ErrorMessages] || '未知错误';
}

/**
 * 创建错误实例的工厂函数
 */
export class ErrorFactory {
  /**
   * 创建数据库错误
   */
  static createDatabaseError(code: string, message?: string, details?: any): DatabaseError {
    return new DatabaseError(
      code,
      message || getErrorMessage(code),
      details
    );
  }

  /**
   * 创建安全错误
   */
  static createSecurityError(code: string, message?: string, details?: any): SecurityError {
    return new SecurityError(
      code,
      message || getErrorMessage(code),
      details
    );
  }

  /**
   * 创建验证错误
   */
  static createValidationError(code: string, message?: string, details?: any): ValidationError {
    return new ValidationError(
      code,
      message || getErrorMessage(code),
      details
    );
  }

  /**
   * 创建数据错误
   */
  static createDataError(code: string, message?: string, details?: any): DataError {
    return new DataError(
      code,
      message || getErrorMessage(code),
      details
    );
  }

  /**
   * 从原始错误创建适当的错误类型
   */
  static fromError(error: any, context?: string): MCPError {
    if (error instanceof MCPError) {
      return error;
    }

    // 根据错误特征判断错误类型
    const errorMessage = error.message || String(error);
    
    if (error.code?.startsWith('ER_') || error.code?.startsWith('ECONNREFUSED')) {
      return new DatabaseError(
        error.code || ErrorCodes.DATABASE.CONNECTION_FAILED,
        errorMessage,
        { originalError: error, context }
      );
    }

    if (error.code === 'ETIMEDOUT' || error.code === 'ENOTFOUND') {
      return new NetworkError(
        error.code,
        errorMessage,
        { originalError: error, context }
      );
    }

    // 默认返回通用错误
    return new DatabaseError(
      ErrorCodes.GENERAL.UNKNOWN_ERROR,
      errorMessage,
      { originalError: error, context }
    );
  }
}